import Vue from 'vue';
import Vuex from 'vuex';
import Constant from './Constant.js';
import axios from './axios-common';

Vue.use(Vuex);
const store = new Vuex.Store({
//state ...
state:{
qnalist:[],
qna:{}
},

actions: { // 외부와의 통신을 통해 데이터 처리 dispath에 의해 호출됨.

[Constant.GET_QNALIST]: store => {
console.log('test');

axios
.get("/safefood/qna/all")
.then(response=>{
store.commit(Constant.GET_QNALIST,{qnalist:response.data});
console.log(response.data);})
.catch(exp => alert("getQnaList처리에 실패하였습니다. "+exp));
},

[Constant.GET_QNA]: (store, payload) => {
console.log('여기는 get', payload.num);

axios
.get("/safefood/qna/read/"+payload.num)
.then(response=>{
store.commit(Constant.GET_QNA,{qna:response.data});
console.log(response.data);})
.catch(exp => alert("get_qna 처리에 실패하였습니다. "+exp));

},
[Constant.ADD_QNA]: (store, payload) => {
console.log(payload);
console.log('add 되나여');

axios
.post("/safefood/qna/insert/", payload.qna)
.then(response=>{
console.log('Real');
store.dispatch(Constant.GET_QNALIST);
console.log(response.data);})
.catch(exp => alert("postqnalist처리에 실패하였습니다. "+exp));
},
[Constant.REMOVE_QNA]: (store, payload) => {
console.log('여기는 delete!');
console.log(payload.num);

axios
.delete("/safefood/qna/delete/"+payload.num)
.then(response=>{
store.dispatch(Constant.GET_QNALIST);
console.log(response.data);})
.catch(exp => alert("deleteqna처리에 실패하였습니다. "+exp));

},
[Constant.ADD_COMMENT]: (store, payload) => {
console.log(payload.qna);
axios
.put("/safefood/qna/insertcomment/",{num:payload.num,comment:payload.comment})
.then(response=>{
store.dispatch(Constant.GET_QNALIST);
console.log(response.data);})
.catch(exp => alert("putqnacomment처리에 실패하였습니다. "+exp));
},


[Constant.UPDATE_QNA]: (store, payload) => {
axios
.put("/safefood/qna/update/",{id:payload.id, num:payload.num, name:payload.name, wdate:payload.wdate, title:payload.title, content:payload.content, count:payload.count, comment: payload.comment})
.then(response=>{
store.dispatch(Constant.GET_QNALIST);
console.log(response.data);})
.catch(exp => alert("updateqna처리에 실패하였습니다. "+exp));

}

},
mutations: { // 저장소에 데이터 실제 반영(commit시 호출됨)

[Constant.GET_QNALIST]: (state, payload) => {
state.qnalist=payload.qnalist;
},
[Constant.GET_QNA]: (state, payload) => {
console.log('커밋');
state.qna=payload.qna;
},

[Constant.ADD_COMMENT]: (state, payload) => {
state.message = payload.message;

}

}
});


export default store;

